<template>
	<div class="m-detail">
		<Edit v-if="curMode!=='content'" :edit-prop="curMode==='edit'?details:null" :curMode="curMode"></Edit>
		<div v-if="curMode==='content'">
			<div class="detail-title">
				<h1>{{details&&details.name}}</h1><i class="edit" @click="editTodo"></i><i class="complete" @click="complete"></i>
			</div>
			<div class="detail-date">
				<p>任务日期：{{details&&details.date}}</p>
			</div>
			<div class="detail-content">
				<p>{{details&&details.content}}</p>
			</div>
		</div>
	</div>
</template>

<script>
    var mode = {
        content: 'content',
        create: 'create',
        edit: 'edit'
    };
    import Edit from './Edit'
    export default {
        data: function() {
            return {
                details: {},
                curMode: mode.content
            }
        },
        created: function() {
            var that = this;
            bus.$on('upDateDetails', function(data) {
                that.details = data;
                bus.$emit('saveData');
            });
            bus.$on('confirm', function(data, curMode) {
                if (curMode === "edit") {
                    for (var o in data) {
                        that.details[o] = data[o];
                    }
                    bus.$emit('saveData');
                }
                that.curMode = mode.content;
            });
            bus.$on('cancel', function() {
                that.curMode = mode.content;
            });
            bus.$on('createNew', function() {
                that.curMode = mode.create;
            });
        },
        methods: {
            complete: function() {
                var c = confirm('确认要完成这项任务吗？');
                if (c) {
                    this.details.completed = true;
                    bus.$emit('saveData');
                }
            },
            editTodo: function() {
                this.curMode = mode.edit;
            }
        },
        components: {
            Edit
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
	.m-detail {
		height: 100%;
		position: absolute;
		left: 462px;
		right: 0;
		background-color: #fff;
	}
	
	.m-detail .detail-title {
		width: 100%;
		line-height: 50px;
		height: 50px;
		background-color: #f2f2f2;
	}
	
	.m-detail h1 {
		display: inline-block;
		margin-left: 16px;
		font-size: 16px;
	}
	
	.m-detail .detail-date {
		width: 100%;
		line-height: 52px;
		height: 52px;
		background-color: #fafaee;
	}
	
	.m-detail p {
		margin-left: 16px;
	}
	
	.m-detail .detail-content {
		width: 100%;
		margin: 16px;
	}
	
	.m-detail .complete {
		width: 25px;
		height: 25px;
		background: url(../assets/img/sprite.png) -51px 0 no-repeat;
		float: right;
		margin: 12px 23px auto auto;
	}
	
	.m-detail .edit {
		width: 25px;
		height: 25px;
		background: url(../assets/img/sprite.png) -26px 0 no-repeat;
		float: right;
		margin: 12px 21px auto auto;
	}
</style>


