<template>
	<form class="detail-edit">
		<h1>{{titleName[curMode]}}</h1>
		<label>任务名称<input type="text" name="title" placeholder="请输入任务名称" v-model="data.name" @blur="validName" @focus="validText.name=''">
        <p>{{validText.name}}</p></label>
		<label>完成日期<input type="text" name="date"  placeholder="请按yyyy-mm-dd的格式输入日期" v-model="data.date" @blur="validDate" @focus="validText.date=''">
        <p>{{validText.date}}</p></label>
		<label>具体内容<textarea rows="3" cols="10" placeholder="请输入任务内容" v-model="data.content" @blur="validContent" @focus="validText.content=''"></textarea>
        <p>{{validText.content}}</p></label>
		<button @click.stop.prevent="confirm">确认</button><button @click.stop.prevent="cancel">取消</button>
	</form>
</template>

<script>
    export default {
        props: {
            editProp: {
                type: Object
            },
            curMode: {
                type: String,
                default: 'create'
            }
        },
        data: function() {
            var data = {};
            for (var key in this.editProp) {
                data[key] = this.editProp[key];
            }
            return {
                data: data,
                titleName: {
                    create: '新建任务',
                    edit: '编辑任务'
                },
                validText: {
                    name: '',
                    date: '',
                    content: '',
                },
                validRules: {
                    name: '^.{1,20}$',
                    date: '^\\d{4}-(0[1-9]|1[0-2])-(0[1-9]|[1-2][0-9]|3[0-1])$',
                    content: '^.{1,100}$',
                },
                validHint: {
                    name: '任务名称不得超过20个字符',
                    date: '请输入为格式yyyy-mm--dd的合法日期',
                    content: '任务具体内容不得超过100个字符',
                }
            };
        },
        methods: {
            confirm: function() {
                if (this.validName() && this.validDate() && this.validContent()) bus.$emit('confirm', this.data, this.curMode);
            },
            cancel: function() {
                bus.$emit('cancel');
            },
            validName: function() {
                var value = this.data.name;
                if (new RegExp(this.validRules.name).test(value)) {
                    this.validText.name = '';
                    return true;
                }
                if (!value) this.validText.name = "请输入任务名称";
                else this.validText.name = this.validHint.name;
                return false;
            },
            validDate: function() {
                var value = this.data.date;
                if (new RegExp(this.validRules.date).test(value) && new Date(value).getDate() == value.substring(value.length - 2)) {
                    this.validText.date = '';
                    return true;
                }
                if (!value) this.validText.date = "请输入任务完成日期";
                else this.validText.date = this.validHint.date;
                return false;
            },
            validContent: function() {
                var value = this.data.content;
                if (new RegExp(this.validRules.content).test(value)) {
                    this.validText.content = '';
                    return true;
                }
                if (!value) this.validText.content = "请输入任务具体内容";
                else this.validText.content = this.validHint.content;
                return false;
            },
        },

    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.detail-edit {
		width: 400px;
		padding: 20px;
		position: absolute;
		top: 20px;
		left: 50%;
		transform: translateX(-50%);
		text-align: center;
		background-color: #f2f2f2;
		border: 1px solid #cecece;
	}
	
	.detail-edit h1 {
		width: 100%;
		margin: 10px 0;
		font-weight: normal;
		font-size: 18px;
	}
	
	.detail-edit label {
		display: block;
	}
	
	.detail-edit p {
		font-size: 12px;
		color: red;
		margin-left: 130px;
		text-align: left;
		height: 15px;
	}
	
	.detail-edit input,
	.detail-edit textarea {
		font-size: 16px;
		width: 190px;
		line-height: 30px;
		margin: 15px 25px;
		padding: 2px 6px;
		font-family: Arial, Hiragino Sans GB, \5fae\8f6f\96c5\9ed1, Helvetica, sans-serif;
		color: #333;
	}
	
	.detail-edit textarea {
		line-height: 25px;
		color: #333;
		vertical-align: middle;
	}
	
	.detail-edit button {
		display: inline-block;
		width: 80px;
		height: 30px;
		margin: 20px 30px 15px;
		font-size: 16px;
		font-family: Arial, Hiragino Sans GB, \5fae\8f6f\96c5\9ed1, Helvetica, sans-serif;
		color: #333;
	}
</style>


