<template>
	<div class="m-category">
		<div>
			<p class="category-all">所有任务&nbsp;&nbsp;({{totalTodoNum}})</p>
			<p>分类列表</p>
			<ul v-for="(category,index) in categories" class="category-list">
				<li class="category-item"><i class="folder"></i><span @click="categoryClick(index)">{{category.name}}&nbsp;&nbsp;({{categoryTodoNum[index]}})</span>
					<i class="delete" @click="deleteCategory(index)"></i>
					<ul v-for="(task,taskIndex) in category.tasks">
						<li v-show="isShow(index,task.todos)" :class="{selected:isSelected(index,taskIndex)}" @click="selectTask(index,taskIndex)">
							<i class="file"></i>{{task.name}}&nbsp;&nbsp;({{unCompletedNum(task.todos)}})
						</li>
					</ul>
				</li>
			</ul>
		</div>
		<div class="category-add" @click="addTask"><i class="add"></i><span>新增分类</span></div>
	</div>
</template>

<script>
    var allData = (window.localStorage['GTDdata'] && JSON.parse(window.localStorage['GTDdata'])) || [{
        name: '百度IFE项目',
        tasks: [{
            name: 'task1',
            todos: [{
                name: 'to-do 1',
                date: '2015-04-28',
                content: '完成task1的编码工作',
                completed: false
            }, {
                name: 'to-do 2',
                date: '2015-04-28',
                content: '完成task2的编码工作',
                completed: false
            }, {
                name: 'to-do 3',
                date: '2015-04-29',
                content: '完成task3的编码工作',
                completed: false
            }, {
                name: 'to-do 4',
                date: '2015-04-29',
                content: '完成task4的编码工作',
                completed: false
            }, {
                name: 'to-do 5',
                date: '2015-04-30',
                content: '完成task5的编码工作',
                completed: false
            }, {
                name: 'to-do 6',
                date: '2015-04-30',
                content: '完成task6的编码工作',
                completed: false
            }]
        }, {
            name: 'task2',
            todos: [{
                name: 'to-do 1',
                date: '2015-04-28',
                content: '完成task7的编码工作',
                completed: false
            }, {
                name: 'to-do 2',
                date: '2015-04-28',
                content: '完成task8的编码工作',
                completed: false
            }, {
                name: 'to-do 3',
                date: '2015-04-29',
                content: '完成task9的编码工作',
                completed: false
            }, {
                name: 'to-do 4',
                date: '2015-04-29',
                content: '完成task10的编码工作',
                completed: false
            }]
        }]
    }, {
        name: '毕业设计',
        tasks: [{
            name: '毕业设计',
            todos: [{
                name: '完成开题报告',
                date: '2015-04-29',
                content: '完成开题报告的copy工作',
                completed: false
            }]
        }]
    }, {
        name: '社团活动',
        tasks: [{
            name: '出卖灵魂',
            todos: [{
                name: '出本子',
                date: '2015-04-29',
                content: '出扶她本子',
                completed: false
            }]
        }]
    }, {
        name: '家庭生活',
        tasks: [{
            name: '日常',
            todos: [{
                name: '打滚',
                date: '2015-04-29',
                content: '在床上抱着抱枕打滚',
                completed: false
            }]
        }]
    }, {
        name: '默认分类',
        tasks: [{
            name: '默认任务',
            todos: [{
                name: '发呆',
                date: '2015-04-29',
                content: '只是在发呆而已',
                completed: false
            }]
        }]
    }];

    export default {
        data: function() {
            return {
                categories: allData,
                selectedIndex: {
                    category: allData.length - 1, // 当前选中分类
                    categorySpread: [allData.length - 1], // 当前展开分类，选中分类必然展开
                    task: 0, // 当前选中任务
                }
            }
        },
        computed: {
            tasks: function() { // 当前分类任务数据
                return this.categories && this.categories[this.selectedIndex.category] && this.categories[this.selectedIndex.category]['tasks'];
            },
            todos: function() { // 当前任务todos数据
                return this.tasks && this.tasks[this.selectedIndex.task] && this.tasks[this.selectedIndex.task].todos;
            },
            categoryTodoNum: function() { // 分类下todo数量
                var res = [];
                var that = this;
                this.categories.forEach(function(category) {
                    var num = 0;
                    category['tasks'].forEach(function(task) {
                        num += that.unCompletedNum(task.todos);
                    });
                    res.push(num);
                });
                return res;
            },
            totalTodoNum: function() { //todo总数
                return this.categoryTodoNum.length > 0 ? this.categoryTodoNum.reduce(function(pre, cur) {
                    return pre + cur;
                }) : 0;
            }
        },
        watch: {
            categories: function() {
                bus.$emit('saveData');
            },
            todos: function(newTodos) { //监听todos变化 传递数据给相应组件
                bus.$emit('upDateTodos', newTodos);
            },
        },
        methods: {
            unCompletedNum: function(todos) {
                var res = 0;
                todos.forEach(function(todo) {
                    if (!todo.completed) res++;
                });
                return res;
            },
            categoryClick: function(index) { //点击分类事件
                if (index !== this.selectedIndex.category) { //选中分类
                    this.selectedIndex.category = index;
                }
                var spreadIndex = this.selectedIndex.categorySpread.indexOf(index); //展开分类
                if (spreadIndex < 0) {
                    this.selectedIndex.categorySpread.push(index);
                } else this.selectedIndex.categorySpread.splice(spreadIndex, 1);
                this.selectedIndex.task = 0; //默认选中第一个任务
                this.selectTask(index, this.selectedIndex.task);
            },
            isShow: function(index, todos) { //判断分类下任务是否展开显示
                return this.selectedIndex.categorySpread.indexOf(index) >= 0 && todos.length >= 0;
            },
            deleteCategory: function(index) { //删除一个分类
                if (index === this.categories.length - 1) alert('默认分类不能删除');
                else {
                    var c = confirm('确认要删除这个分类吗');
                    if (c) this.categories.splice(index, 1);
                    if (index === this.selectedIndex.category) {
                        this.selectedIndex.category = this.categories.length - 1;
                    }
                    this.selectedIndex.categorySpread.forEach(function(item, index, array) { //对应index调整
                        if (index <= item) array[index]--;
                    });
                    if (index <= this.selectedIndex.category) {
                        this.selectedIndex.category--;
                    }
                }
            },
            selectTask: function(categoryIndex, taskIndex) {
                this.selectedIndex.category = categoryIndex;
                this.selectedIndex.task = taskIndex;
            },
            isSelected: function(categoryIndex, taskIndex) {
                return categoryIndex === this.selectedIndex.category && taskIndex === this.selectedIndex.task;
            },
            addTask: function() {
                if (this.selectedIndex.category === this.categories.length - 1) alert('默认分类不能添加子分类');
                else {
                    var p = prompt('请输入子分类名称', 'task1');
                    if (p !== null && p !== '') {
                        this.categories[this.selectedIndex.category].tasks.push({
                            name: p,
                            todos: []
                        });
                    }
                    this.selectedIndex.task = this.categories[this.selectedIndex.category].tasks.length - 1;
                }
            }
        },
        created: function() {
            function saveData() {
                window.localStorage.setItem('GTDdata', JSON.stringify(this.categories));
            }
            bus.$on('saveData', saveData.bind(this));
        },
        mounted: function() { //挂载后进行数据的初始化
            bus.$emit('upDateTodos', this.todos);
        }
    }
    /*
    vuex: {
        getters: { // 指定当前组件能从 store 里获取哪些状态信息的地方
            // 必须是纯函数，完全依赖输入值，不能有this，单开一个computed属性
            count: function(state) {
                return state.count;
            },
            action: increment
        }
    }
    } */
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
	.m-category {
		float: left;
		width: 230px;
		height: 100%;
		background-color: #f2f2f2;
		border-right: 1px solid #cecece;
	}
	
	.m-category p {
		margin: 10px 0 10px 12px;
	}
	
	.m-category>div {
		padding: 28px 0;
	}
	
	.m-category .category-all {
		margin-bottom: 20px;
	}
	
	.category-list {
		margin: 7px 0;
		cursor: pointer;
	}
	
	.category-list ul {
		margin: 7px 0;
	}
	
	.category-list .category-item {
		padding-left: 19px;
	}
	
	.category-list .category-item li {
		padding: 8px 0;
		padding-left: 44px;
		margin-left: -19px;
	}
	
	.category-list .category-item li.selected {
		background-color: #fff;
		background-origin: border-box;
	}
	
	.m-category .category-add {
		width: 174px;
		border-top: 1px solid #cecece;
		padding: 14px 28px;
		position: absolute;
		bottom: 0;
		cursor: pointer;
	}
	
	.category-add span {
		margin-left: 10px;
		vertical-align: middle;
	}
	
	i {
		display: inline-block;
	}
	
	.folder {
		width: 16px;
		height: 11px;
		background: url(../assets/img/sprite.png) 0 0 no-repeat;
		margin-right: 16px;
	}
	
	.file {
		width: 10px;
		height: 13px;
		background: url(../assets/img/sprite.png) -16px 0 no-repeat;
		margin-right: 11px;
	}
	
	.delete {
		height: 12px;
		width: 12px;
		visibility: hidden;
		position: relative;
		float: right;
		margin-right: 12px;
		cursor: pointer;
	}
	
	.category-item:hover .delete {
		visibility: visible;
	}
	
	.delete:before,
	.delete:after {
		content: '';
		height: 3px;
		width: 12px;
		display: block;
		background: #333;
		border-radius: 5px;
		-webkit-border-radius: 5px;
		-moz-border-radius: 5px;
		position: absolute;
		top: 7px;
		left: 0px;
		transform: rotate(-45deg);
		-webkit-transform: rotate(-45deg);
		-moz-transform: rotate(-45deg);
		-o-transform: rotate(-45deg);
		-ms-transform: rotate(-45deg);
	}
	
	.delete:after {
		transform: rotate(45deg);
		-webkit-transform: rotate(45deg);
		-moz-transform: rotate(45deg);
		-o-transform: rotate(45deg);
		-ms-transform: rotate(45deg);
	}
	
	.add {
		height: 18px;
		width: 18px;
		position: relative;
		vertical-align: middle;
	}
	
	.add:before,
	.add:after {
		content: '';
		height: 4px;
		width: 18px;
		display: block;
		background: #333;
		border-radius: 5px;
		-webkit-border-radius: 5px;
		-moz-border-radius: 5px;
		position: absolute;
		top: 6px;
		left: 0px;
	}
	
	.add:after {
		height: 18px;
		width: 4px;
		top: -1px;
		left: 7px;
	}
</style>


