<template>
	<div class="m-todos">
		<p><span @click="showMode=0" :class="{selected:showMode===0}">所有</span>
			<span@click="showMode=1" :class="{selected:showMode===1}">未完成</span>
				<span@click="showMode=2" :class="{selected:showMode===2}">已完成</span>
		</p>
		<ul v-for="(todo,index) in todos">
			<li v-show="isDateShow(todo,index)" class="todos-date">{{todo.date}}</li>
			<li v-show="isShow(todo)" class="todos-item" @click="selectedIndex=index" :class="{selected:selectedIndex===index,
			completed:todo.completed||(details.completed&&selectedIndex===index)}">{{todo.name}}</li>
		</ul>
		<div class="todos-add" @click="createNewTodo"><i class="add"></i><span>新增任务</span></div>
	</div>
</template>

<script>
    export default {
        data: function() {
            return {
                todos: [],
                selectedIndex: 0,
                showMode: 0 //0所有 1未完成 2 已完成
            }
        },
        created: function() {
            var that = this;
            bus.$on('upDateTodos', function(data) {
                that.todos = data;
                bus.$emit('saveData');
            });
            bus.$on('confirm', function(data, curMode) {
                if (curMode === 'create') {
                    that.todos.push(data);
                    that.sort();
                    that.selectedIndex = that.todos.indexOf(data);
                    bus.$emit('saveData');
                }
            });
        },
        computed: {
            details: function() { // 当前todo 详细数据 计算属性中的语句不一定会执行
                return this.todos && this.todos[this.selectedIndex];
            },
        },
        watch: { //监听details变化 传递数据给相应组件
            details: function(newDetails) {
                bus.$emit('upDateDetails', newDetails);
            }
        },
        methods: {
            isShow: function(todo) { //是否显示列表项
                return !this.showMode ? true : (this.showMode === 1 ? !todo.completed : todo.completed);
            },
            isDateShow: (function() { //是否显示日期 日期只显示一次
                var date = [];
                return function(todo, index) {
                    if (!index) date = [];
                    if (date.indexOf(todo.date) >= 0) return false;
                    else date.push(todo.date);
                    return this.isShow(todo) && true;
                };
            }()),
            createNewTodo: function() { //创建新任务
                bus.$emit('createNew');
            },
            sort: function() {
                this.todos.sort(function(a, b) {
                    return new Date(a.date) > new Date(b.date);
                });
            }
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
	.m-todos {
		float: left;
		width: 230px;
		height: 100%;
		background-color: #fff;
		border-right: 1px solid #cecece;
	}
	
	.m-todos p {
		height: 22px;
		padding: 8px 10px;
		background-color: #eee;
		border-bottom: 1px solid #cecece;
	}
	
	.m-todos p span {
		display: inline-block;
		padding: 3px 10px;
		border: 1px solid transparent;
		cursor: pointer;
	}
	
	.m-todos span.selected {
		background-color: #fff;
		border: 1px solid #cecece;
	}
	
	.m-todos li {
		padding: 9px 20px;
	}
	
	.m-todos .todos-item {
		cursor: pointer;
	}
	
	.m-todos .todos-item.completed {
		color: #2a8e0a;
	}
	
	.m-todos .todos-date {
		background-color: #fafaee;
	}
	
	.m-todos .selected {
		background-color: #f2f2f2;
	}
	
	.m-todos .todos-add {
		width: 174px;
		border-top: 1px solid #cecece;
		padding: 14px 28px;
		position: absolute;
		bottom: 0;
		background-color: #f2f2f2;
		cursor: pointer;
	}
	
	.todos-add span {
		margin-left: 10px;
		vertical-align: middle;
	}
</style>


